.Sidebar {
   top: 8rem;
   left: 1.5rem;
   position: fixed;
   width: 17em;
   height: auto;
   z-index: 5;
}

.Sidebar .content {
   width: 100%;
   height: 100%;
   display: flex;
   align-items: center;
   flex-direction: column;
}

.Sidebar .content .txt {
   display: flex;
   align-items: center;
   justify-content: center;
   height: 20%;
}

.Sidebar .content h1,
.Sidebar .content h2 {
   text-align: center;
   font-size: 1.7rem;
}

.Sidebar .content .btns {
   width: 100%;
   height: calc(100% / 3);
   display: flex;
   align-items: center;
   justify-content: flex-start;
   flex-direction: column;
   gap: 1rem;
}

.Sidebar .content .btns .btn {
   width: 90%;
   height: 3rem;
   background-color: ghostwhite;
   color: #343b49;
   display: flex;
   align-items: center;
   font-size: 1.2rem;
   border-radius: 1rem;
   cursor: pointer;
}

.Sidebar .content .btns .btn.active {
   filter: invert(1);
}

.Sidebar .content .btns .btn .icon-btn {
   margin: 0 1rem;
}

.Sidebar .content .btns .btn:hover,
.Sidebar .content .btn-salir .btn:hover {
   filter: invert(1);
}


.Sidebar .content .btn-salir {
   height: calc(100% / 3);
   width: 100%;
   display: flex;
   justify-content: center;
   align-items: flex-end;

}

.Sidebar .content .btn-salir .btn {
   width: 50%;
   height: 3rem;
   background-color: #343b49;
   color: ghostwhite;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 1.2rem;
   border: 1px solid ghostwhite;
   border-radius: 100rem;
   gap: 0.5rem;
   cursor: pointer;
}